<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="140px">
        <el-form-item label="" prop="contactName">
          <el-input v-model="queryForm.contactName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="contactPhone">
          <el-input v-model="queryForm.contactPhone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="queryForm.status">
            <el-option label="" value=""></el-option>
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="queryForm.playStatus">
            <el-option label="" value=""></el-option>
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="chooseDate">
          <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="" start-placeholder="" end-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button @click="resetForm('queryForm')"></el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="fileUrl" label="" width="100">
            <template slot-scope="scope">
              <img width="80" :src="imgPath(scope.row.fileUrl)" alt="" />
            </template>
          </el-table-column>
          <el-table-column prop="contactName" label="" width="100"></el-table-column>
          <el-table-column prop="contactPhone" label="" width="120"></el-table-column>
          <el-table-column prop="userName" label="" width="100"></el-table-column>
          <el-table-column prop="note" label="" show-overflow-tooltip></el-table-column>
          <el-table-column prop="createDate" label="" width="180"></el-table-column>
          <!--<el-table-column prop="linkHref" label=""></el-table-column>-->
          <!--<el-table-column prop="auditName" label="" width="100"></el-table-column>-->
          <!--<el-table-column prop="auditNote" label="" width="200"></el-table-column>-->
          <!--<el-table-column prop="auditDate" label="" width="150"></el-table-column>-->
          <el-table-column prop="status" label="" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status == 0"></el-tag>
              <el-tag type="success" v-else-if="scope.row.status == 1"></el-tag>
              <el-tag type="danger" v-else></el-tag>
            </template>
          </el-table-column>

          <el-table-column prop="playStatus" label="" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.playStatus == 1"></el-tag>
              <el-tag type="danger" v-else></el-tag>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="detail(scope.row)"></el-link>
              <el-link v-if="scope.row.playStatus === 1" :underline="false" type="danger" @click.stop="close(scope.row.id)"></el-link>
              <el-link v-else :underline="false" type="primary" @click.stop="open(scope.row.id)"></el-link>
              <el-link v-if="scope.row.status === 0" :underline="false" type="primary" @click.stop="audit(scope.row)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      queryForm: {
        status: '',
        playStatus: '',
        contactName: '',
        contactPhone: '',
        startDate: '',
        endDate: '',
        chooseDate: '',
        size: 10,
        page: 1,
        total: ''
      },
      tableData: []
    }
  },
  methods: {
    changeDate(e) {
      if (!e) {
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      } else {
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      console.log(this.queryForm)
    },
    open(id) {
      this.$confirm('?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/advertisement/enable/' + id, '').then(res => {
            this.$message.success('')
            this.loadData()
          })
        })
        .catch(() => {})
    },

    close(id) {
      this.$confirm('?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/advertisement/disable/' + id, '').then(res => {
            this.$message.success('')
            this.loadData()
          })
        })
        .catch(() => {})
    },
    imgPath(thumbnail) {
      if (!thumbnail) return ''
      return this.$adDownload + thumbnail
    },

    // 
    audit(info) {
      info.type = 'audit'
      this.$router.push({ path: '/auditAdvertisement', name: 'AuditAdvertisement', params: { type: 'audit', info } })
      this.$sessionData('set', 'info', info)
    },
    // 
    detail(info) {
      info.type = 'detail'
      this.$router.push({ path: '/auditAdvertisement', name: 'AuditAdvertisement', params: { type: 'detail', info } })
      this.$sessionData('set', 'info', info)
    },

    del(id) {
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/advertisement/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/advertisement/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 300px !important;
  }
  .el-date-editor {
    width: 300px;
  }
}
</style>
