<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/committee' }"></el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '' : $route.params.type === 'next' ? '' : '' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <span class="title"></span>
        <div class="base_info">
          <div>
            <el-form-item label="" prop="cnName">
              <el-input v-model="ruleForm.cnName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="shortName">
              <el-input v-model="ruleForm.shortName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="enName">
              <el-input v-model="ruleForm.enName" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="" prop="clazz">
              <el-input v-model="ruleForm.clazz" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="code">
              <el-input v-model="ruleForm.code" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="range">
              <el-input v-model="ruleForm.range" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="" prop="prepareOrg">
              <el-input v-model.number="ruleForm.prepareOrg" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="guidanceOrg">
              <el-input v-model="ruleForm.guidanceOrg" placeholder=""></el-input>
            </el-form-item>
          </div>
        </div>

        <span class="title"></span>
        <div class="secretiral">
          <div>
            <el-form-item label="" prop="secretary">
              <el-input v-model="ruleForm.secretary" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="contact">
              <el-input v-model="ruleForm.contact" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="company">
              <el-input v-model="ruleForm.company" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="()" prop="province">
              <el-input v-model="ruleForm.province" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="address">
              <el-input v-model="ruleForm.address" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="ruleForm.phone" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="" prop="fax">
              <el-input v-model="ruleForm.fax" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="zipCode">
              <el-input v-model="ruleForm.zipCode" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="ruleForm.email" placeholder=""></el-input>
            </el-form-item>
          </div>
        </div>

        <span class="title"></span>
        <el-tag :key="tag" v-for="tag in dynamicTags" closable :disable-transitions="false" @close="handleClose(tag)">
          {{ tag }}
        </el-tag>
        <el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm"> </el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInput">+  </el-button>

        <el-form-item class="fn_btn">
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      ruleForm: {
        code: '',
        id: '',
        cnName: '',
        range: '',
        enName: '',
        clazz: '',
        shortName: '',
        secretaryId: '',
        prepareOrg: '',
        guidanceOrg: '',
        secretary: '',
        contact: '',
        company: '',
        phone: '',
        province: '',
        email: '',
        address: '',
        fax: '',
        zipCode: '',
        memberList: ''
      },
      dynamicTags: [],
      inputVisible: false,
      inputValue: '',
      rules: {
        code: [{ required: true, message: '', trigger: 'blur' }],
        cnName: [{ required: true, message: '', trigger: 'blur' }],
        range: [{ required: true, message: '', trigger: 'blur' }],
        enName: [{ required: true, message: '', trigger: 'blur' }],
        clazz: [{ required: true, message: '', trigger: 'blur' }],
        shortName: [{ required: true, message: '', trigger: 'blur' }],
        prepareOrg: [{ required: true, message: '', trigger: 'blur' }],
        guidanceOrg: [{ required: true, message: '', trigger: 'blur' }],
        secretary: [{ required: true, message: '', trigger: 'blur' }],
        contact: [{ required: true, message: '', trigger: 'blur' }],
        company: [{ required: true, message: '', trigger: 'blur' }],
        phone: [{ required: true, message: '', trigger: 'blur' }],
        province: [{ required: true, message: '', trigger: 'blur' }],
        email: [{ required: true, message: '', trigger: 'blur' }],
        address: [{ required: true, message: '', trigger: 'blur' }],
        fax: [{ required: true, message: '', trigger: 'blur' }],
        zipCode: [{ required: true, message: '', trigger: 'blur' }]
      },
      type: ''
    }
  },
  methods: {
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1)
    },

    showInput() {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },

    handleInputConfirm() {
      let inputValue = this.inputValue
      if (inputValue) {
        this.dynamicTags.includes(inputValue) ? this.$message.warning('') : this.dynamicTags.push(inputValue)
      }
      this.inputVisible = false
      this.inputValue = ''
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.ruleForm.memberList = JSON.stringify(this.dynamicTags)
          let params = Object.assign({}, this.ruleForm)
          if (this.$route.params.type === 'edit') {
            this.httpPost('/committee/mod/' + params.id, params).then(res => {
              this.$message.success('')
              this.resetForm('ruleForm')
            })
          } else if (this.$route.params.type === 'next') {
            this.httpPost('/committee/add/clazz/' + params.id, params).then(res => {
              this.$message.success('')
              this.resetForm('ruleForm')
            })
          } else {
            this.httpPost('/committee/add', params).then(res => {
              this.$message.success('')
              this.resetForm('ruleForm')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$router.back()
      this.$refs[formName].resetFields()
    },
    detail() {
      const { params } = this.$route
      this.httpGet('/committee/detail/' + params.info.id, '').then(res => {
        this.ruleForm = res
        this.dynamicTags = eval(res.memberList)
        if (params.type === 'next') this.ruleForm.clazz = parseInt(this.ruleForm.clazz) + 1
      })
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      this.detail()
      this.type = 2
    } else if (params && params.type === 'next') {
      this.detail()
      this.type = 3
    } else {
      this.type = 1
    }
  }
}
</script>

<style lang="scss" scoped>
.el-from {
}
/deep/.el-form-item {
  .el-input {
    width: 300px !important;
  }
  .el-textarea {
    width: 300px;
  }
}
.el-cascader {
  width: 300px;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}

.title {
  font-weight: bold;
  display: block;
  margin-bottom: 20px;
}
.secretiral,
.person,
.base_info {
  display: flex;
}
.el-tag {
  margin-left: 10px;
}
.el-tag,
.input-new-tag {
  margin-bottom: 10px;
}
.fn_btn /deep/.el-form-item__content {
  padding-top: 20px;
  margin-left: 0px !important;
}
</style>
