<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :inline="true" ref="queryForm" :model="queryForm" label-width="140px">
        <!-- <el-form-item label="" prop="keyword">
          <el-input v-model="queryForm.keyword" placeholder=""></el-input>
        </el-form-item> -->
        <el-form-item label="" prop="title">
          <el-input v-model="queryForm.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="queryForm.status">
            <el-option label="" value=""></el-option>
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button @click="resetForm('queryForm')"></el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <div class="btn">
          <el-button @click.stop="add" icon="el-icon-plus"></el-button>
          <el-button @click.stop="sort" icon="el-icon-plus"></el-button>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="rownum_" label="" width="100"></el-table-column>
          <el-table-column prop="title" label="" width="200"></el-table-column>
          <el-table-column prop="thumbnail" label="" width="200">
            <template slot-scope="scope">
              <img class="thumbnail_img" width="80" @click="videoLink(scope.row)" :src="imgPath(scope.row.thumbnail)" alt="" />
            </template>
          </el-table-column>
          <el-table-column prop="type" label="" width="200">
            <template slot-scope="scope">
              {{ scope.row.type == 1 ? '' : '' }}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{ scope.row.type == 1 ? scope.row.webUrl : scope.row.fileUrl }}
            </template>
          </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status == 1"></el-tag>
              <el-tag type="danger" v-else></el-tag>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="150">
            <template slot-scope="scope">
              <el-link v-if="scope.row.status === 1" :underline="false" type="danger" @click.stop="close(scope.row.id)"></el-link>
              <el-link v-else :underline="false" type="primary" @click.stop="open(scope.row.id)"></el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
      <el-dialog :visible.sync="videoDialog" width="40%">
        <div class="demo">
          <video-player class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions"> </video-player>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      queryForm: {
        status: '',
        title: '',
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      videoDialog: false,
      videoUrl: '',
      playerOptions: {
        //
        playbackRates: [0.5, 1.0, 1.5, 2.0],
        //true,
        autoplay: false,
        // 
        muted: false,
        // 
        loop: false,
        // <video>auto,
        preload: 'auto',
        language: 'zh-CN',
        //  - "16:9""4:3"
        aspectRatio: '16:9',
        // trueVideo.js player
        fluid: true,
        sources: [
          {
            //
            type: 'video/webm', // ogg
            type: 'video/ogg', // webm
            type: 'video/3gp', // 
            type: 'video/mp4', // 
            //url
            src: ''
          }
        ],
        //
        poster: '',
        //Video.js
        notSupportedMessage: '',
        controlBar: {
          timeDivider: true, // 
          durationDisplay: true, // 
          remainingTimeDisplay: false, // 
          fullscreenToggle: true, // 
          volumeControl: true // 
        }
      }
    }
  },
  watch: {
    videoDialog(n, o) {
      if (n === false) {
        // console.log('1')
        // this.$refs.videoPlayer.player.play() // 
        this.$refs.videoPlayer.player.pause() // 
        // this.$refs.videoPlayer.player.src(src) // 
      }
    }
  },
  methods: {
    videoLink(info) {
      this.videoDialog = true
      if (info.type == 1) this.playerOptions.sources[0].src = info.webUrl
      else this.playerOptions.sources[0].src = this.$videoDownload + info.fileUrl
      this.playerOptions.poster = this.imgPath(info.thumbnail)
    },
    open(id) {
      this.$confirm('?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/video/enable/' + id, '').then(res => {
            this.$message.success('')
            this.loadData()
          })
        })
        .catch(() => {})
    },

    close(id) {
      this.$confirm('?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/video/disable/' + id, '').then(res => {
            this.$message.success('')
            this.loadData()
          })
        })
        .catch(() => {})
    },
    imgPath(thumbnail) {
      if (!thumbnail) return ''
      return this.$videoDownload + thumbnail
    },
    sort() {
      this.$router.push({ path: '/standardVideoSort', name: 'StandardVideoSort' })
    },
    // 
    add() {
      this.$router.push({ path: '/editStandardVideo', name: 'EditStandardVideo', params: { type: 'add' } })
    },

    // 
    edit(info) {
      this.$router.push({ path: '/editStandardVideo', name: 'EditStandardVideo', params: { type: 'edit', info } })
    },

    del(id) {
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/video/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/video/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 300px !important;
  }
}
.btn {
  margin-bottom: 10px;
}
.thumbnail_img {
  cursor: pointer;
}

.demo {
  display: inline-block;
  width: 100%;
  height: 400px;
  text-align: center;
  line-height: 100px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  margin-right: 4px;
}

.demo:hover {
  display: block;
}
</style>
