<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :inline="true" ref="queryForm" :model="queryForm" label-width="140px">
        <!-- <el-form-item label="" prop="keyword">
          <el-input v-model="queryForm.keyword" placeholder=""></el-input>
        </el-form-item> -->
        <el-form-item label="" prop="name">
          <el-input v-model="queryForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="queryForm.status">
            <el-option label="" value=""></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button @click="resetForm('queryForm')"></el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <div class="btn">
          <el-button @click.stop="add" icon="el-icon-plus"></el-button>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="thumbnail" label="" >
            <template slot-scope="scope">
              <img class="thumbnail_img" width="80" @click="goLink(scope.row.linkHref)" :src="imgPath(scope.row.fileUrl)" alt="" />
            </template>
          </el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="linkHref" label="" width="200"></el-table-column>
          <el-table-column prop="createDate" label="" width="200"></el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status == 1"></el-tag>
              <el-tag type="danger" v-else></el-tag>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="150">
            <template slot-scope="scope">
              <el-link v-if="scope.row.status == 1"  type="danger" @click.stop="close(scope.row.id)"></el-link>
              <el-link v-else type="primary" @click.stop="open(scope.row.id)"></el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
  export default {
    data() {
      let that = this
      return {
        queryForm: {
          status: '',
          name: '',
          size: 10,
          page: 1,
          total: ''
        },
        tableData: [],
      }
    },
    methods: {
      open(id) {
        this.$confirm('?', '', { type: 'warning' })
          .then(() => {
            this.httpPost('/float/enable/' + id, '').then(res => {
              this.$message.success('')
              this.loadData()
            })
          })
          .catch(() => {})
      },

      close(id) {
        this.$confirm('?', '', { type: 'warning' })
          .then(() => {
            this.httpPost('/float/disable/' + id, '').then(res => {
              this.$message.success('')
              this.loadData()
            })
          })
          .catch(() => {})
      },
      imgPath(thumbnail) {
        if (!thumbnail) return ''
        return this.$videoDownload + thumbnail
      },
      // 
      add() {
        this.$router.push({ path: '/editFloatingPane', name: 'EditFloatingPane', params: { type: 'add' } })
      },

      // 
      edit(info) {
        this.$router.push({ path: '/editFloatingPane', name: 'EditFloatingPane', params: { type: 'edit', info } })
      },

      del(id) {
        this.$confirm(', ?', '', { type: 'warning' })
          .then(() => {
            this.httpPost('/float/rem/' + id, '').then(ret => {
              this.$message({ type: 'success', message: '!' })
              this.loadData()
            })
          })
          .catch(() => {})
      },

      onSubmit() {
        console.log(this.queryForm)
        this.loadData()
      },

      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      goLink(url){
        window.open(url)
      },
      loadData() {
        this.queryForm.total = ''
        this.httpGet('/float/query', this.queryForm).then(res => {
          this.tableData = res.rows
          this.queryForm.total = res.total
        })
      }
    },

    mounted() {
      this.loadData()
    }
  }
</script>

<style lang="scss" scoped>
  /deep/.el-form {
    display: flex;
    flex-wrap: wrap;
    border: 1px solid #ddd;
    padding-top: 10px;
    margin-bottom: 20px;
    .select_btn .el-form-item__content {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      height: 40px;
      box-sizing: border-box;
      .choose_condition {
        padding: 0 10px;
        cursor: pointer;
      }
    }
  }
  /deep/.el-form {
    .el-input {
      width: 300px !important;
    }
  }
  .btn {
    margin-bottom: 10px;
  }
  .thumbnail_img {
    cursor: pointer;
  }

  .demo {
    display: inline-block;
    width: 100%;
    height: 400px;
    text-align: center;
    line-height: 100px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    margin-right: 4px;
  }

  .demo:hover {
    display: block;
  }
</style>
